
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.resources {
    width: 100%;
    float: right;
}
.list {
    width: 25%;
    height: 80vh;
    float: left;
    background-color: rgba(87, 76, 79, 0.5);
}
.pokemon {
    width: 65%;
    float: right;
    background-color: rgba(87, 76, 79, 0.5);
}
td {
    width: 33%;
    cursor: pointer;
}
#title {
    font-weight: bold;
    font-size: 18px;
    color: white;
}
#name {
    color: white;
    text-transform: uppercase;
}
button {
    margin: 6% 3%;
}
